/* eslint-disable no-unused-vars*/

import CodeMirror from '@uiw/react-codemirror';
import { oneDark } from '@codemirror/theme-one-dark';
import { json } from '@codemirror/lang-json';

const EditorInput = ( { exampleValue, setDoc } ) => {

  return (
    <CodeMirror
      value={JSON.stringify(exampleValue, null, ' ')}
      height="80vh"
      minHeight="200px"
      theme={oneDark}
      extensions={[json()]}
      onChange={(value, viewUpdate) => {
        setDoc(value);
      }}
    />
  );
};

const ResponseInput = ( { exampleValue } ) => {

  return (
    <CodeMirror
      value={JSON.stringify(exampleValue, null, ' ')}
      height="40vh"
      maxWidth="70vw"
      theme={oneDark}
      extensions={[json()]}
      onChange={(value, viewUpdate) => {
        // use the function passed from Viewer component to update the value
      }}
    />
  );
};

const RequestInput = ( { exampleValue, setReqBody } ) => {

  return (
    <CodeMirror
      value={JSON.stringify(exampleValue, null, ' ')}
      height="40vh"
      maxWidth="70vw"
      theme={oneDark}
      extensions={[json()]}
      onChange={(value, viewUpdate) => {
        setReqBody(value);
        // use the function passed from Viewer component to update the value
      }}
    />
  );
};

export { EditorInput, ResponseInput, RequestInput };