import { ReqContent } from './Schema';

const Request = ({ requestBody, color, setReqBody, setReqContentType }) => {
  return (
    <div>
      <div className={`bg-light text-${color}`}><h4>Requests</h4></div>
      <div className={`req-body bg-${color}`}>
      {
        requestBody
        ?
        <ReqBody
          color = {color}
          requestBody = {requestBody}
          setReqBody = {setReqBody}
          setReqContentType = {setReqContentType}
        />
        :
        'no request body'
      }
      </div>
    </div>
  );
};

const ReqBody = ({ requestBody, color, setReqBody, setReqContentType }) => {

  const { description, required, content } = requestBody;

  return (
      <div>
        <div>{required ? 'required' : null}</div>
        <div>{description}</div>
        <ReqContent
          color = {color}
          content = {content}
          setReqBody = {setReqBody}
          setReqContentType = {setReqContentType}
        />
      </div>
  );
};

export default Request;