import { ResContent } from './Schema';
import Table from 'react-bootstrap/Table';

const Response = ({ responses, color }) => {
  return (
    <div>
      <div className={`bg-light text-${color}`}><h4>Responses</h4></div>
      <div className={`res-body bg-${color}`}>
      {
        responses
        ?
        <ResBody
          color = {color}
          responses = {responses}
        />
        :
        'no response'
      }
      </div>
  </div>
  );
};

const ResBody = ({ responses, color }) => {

  const statusCodes = Object.keys(responses);

  return (
    <div>
      {
        <Table responsive>
        <thead>
          <tr>
            <th>Name</th>
            <th>Description</th>
          </tr>
        </thead>
        {statusCodes.map((statusCode) => {
          return (
            <StatusBody
              color = {color}
              statusCode = {statusCode}
              statusData = {responses[statusCode]}
              key = {statusCode}
            />
          );
        })}
        </Table>
      }
    </div>
  );
};

const StatusBody = ( { statusCode, statusData, color } ) => {

  const { description, content } = statusData;

  return (
    <tbody>
      <tr>
        <td>
          {statusCode}
        </td>
        <td>
          {description}
          <ResContent
            color = {color}
            content = {content}
          />
        </td>
      </tr>
    </tbody>
  );
};

export default Response;