/* eslint-disable no-unused-vars*/

import { useState, useEffect } from 'react';

import Parameter from '../components/Parameter';
import Request from '../components/Request';
import Response from '../components/Response';
import SecurityBtn from '../utils/SecurityBtn';
import { ResponseInput } from '../utils/Input';

import 'bootstrap/dist/css/bootstrap.min.css';
import Accordion from 'react-bootstrap/Accordion';
import Badge from 'react-bootstrap/Badge';
import Stack from 'react-bootstrap/Stack';
import { Container, Row, Col, Button, InputGroup, FormControl } from 'react-bootstrap';
import Table from 'react-bootstrap/Table';

import axios from 'axios';

/*============ get all tags ============*/
function getTags(docValue) {
  const { paths } = docValue;
  const pathsArr = Object.keys(paths); //pathsarray  let tagArr = [];
  let tagArr = [];
  for (let i = 0; i < pathsArr.length; i++) {
    // console.log(pathsArr[i]); //paths

    const requestsArr = Object.keys(paths[pathsArr[i]]); //requestsarray

    for (let j = 0; j < requestsArr.length; j++) {
      // console.log(requestsArr[j]); //requests

      const { tags } = paths[pathsArr[i]][requestsArr[j]];
      tagArr.push(...tags);
    }
  }
  const uniqueTags = [...new Set(tagArr)];
  return uniqueTags;
}

/*============ get tagTable ============*/
function getTagTable(docValue, TagsData) {
  const { paths } = docValue;
  const pathsArr = Object.keys(paths); //pathsarray  let tagArr = [];

  const tagTable = TagsData.reduce((acc, cur) => {
    if (!acc[cur]) {
      acc[cur] = [];
    }
    for (let i = 0; i < pathsArr.length; i++) {
      const requestsArr = Object.keys(paths[pathsArr[i]]);
      for (let j = 0; j < requestsArr.length; j++) {
        const path = pathsArr[i];
        const requestType =  requestsArr[j];
        const detailObj = paths[pathsArr[i]][requestsArr[j]];
        if (detailObj.tags.includes(cur)) {
          acc[cur].push([path, requestType, detailObj]);
        }
      }
    }
    return acc;
  }, {});
  return tagTable;
}
/*============ TODO: Refactor above since the for loop was done twice ============*/

const Viewer = ( { docValue } ) => {

  const { servers, openapi, info, consumes, produces } = docValue;
  const { version, title, description } = info;

  const uniqueTags = getTags(docValue);
  const tagTable = getTagTable(docValue, uniqueTags);

  const [ server, setServer ] = useState(servers[0].url);

  return (
    <div>
      <Container>
        <ViewerTitle
          servers = {servers}
          openapi = {openapi}
          consumes = {consumes}
          produces = {produces}
          version = {version}
          title = {title}
          description = {description}
          setServer = {setServer}
        />
        <ViewerBody
          uniqueTags = {uniqueTags}
          tagTable = {tagTable}
          server = {server}
        />
      </Container>
    </div>
  );
};

const ViewerTitle = ( { servers, openapi, consumes, produces, version, title, description, setServer } ) => {
  return (
    <>
      <Row>
        <Col>
          <h1>{title}</h1>
          <h6><Badge bg="secondary">{version}</Badge><Badge bg="info">{`OAS ${openapi}`}</Badge></h6>
        </Col>
        <Col xs={6}>
          <blockquote className="blockquote">
            <p className="mb-0">{description}</p>
          </blockquote>
        </Col>
      </Row>
      <select onChange={e => setServer(e.target.value.split(' - ')[0])}>
        {servers.map(server => {
          return <option key={server.url}>{`${server.url} - ${server.description}`}</option>;
        })}
      </select>
    </>
  );
};

const ViewerBody = ( { uniqueTags, tagTable, server } ) => {
  return (
    <>
      {uniqueTags.map(tag =>
        <Tag
          tagName={tag}
          detailArr = {tagTable[tag]}
          server = {server}
          key={tag}
        />
      )}
    </>
  );
};

const Tag = ({ tagName, detailArr, server }) => {

  return (
    <div>
      <Accordion defaultActiveKey={['0']} alwaysOpen>
        <Accordion.Item eventKey="0">
          <Accordion.Header><h4>{tagName}</h4></Accordion.Header>
          <Accordion.Body>
            {detailArr.map(detail =>
              <Body
                detail = {detail}
                server = {server}
                key = {detail[1] + ' ' + detail[0]}
              />
            )}
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>
    </div>
  );
};

const Body = ({ detail, server }) => {

  const path = detail[0];

  const requestType = detail[1];
  const {
    summary,
    produces,
    security,
    parameters,
    requestBody,
    responses,
  } = detail[2];

  function applyColor(requestType) {
    switch (requestType) {
      case 'get':
        setColor('primary');
        break;
      case 'post':
        setColor('success');
        break;
      case 'put':
        setColor('warning');
        break;
      case 'delete':
        setColor('danger');
        break;
      default:
        break;
    }
  }

  const [color, setColor] = useState('');

  useEffect(() => {
    applyColor(requestType);
  }, [requestType]);

  return (
    <div>
      <Title
        color = {color}
        path = {path}
        requestType = {requestType}
        summary = {summary}
        security = {security}
        parameters = {parameters}
        requestBody = {requestBody}
        responses = {responses}
        server = {server}
      />
    </div>
  );
};

const Title = ( { color, path, requestType, summary, security, parameters, requestBody, responses, server } ) => {
  // TODO: apply color to Accordion
  function getInitReqBody(requestBody) {
    if (!requestBody) return '';

    const schemaObj = requestBody.content[Object.keys(requestBody.content)[0]];
    if (!schemaObj.examples && !schemaObj.example) return '';

    if (schemaObj.examples) {
      return schemaObj.examples[Object.keys(schemaObj.examples)[0]].value;
    } else if (schemaObj.example) {
      return schemaObj.example;
    }
  }

  function getInitContentType(requestBody) {
    if (!requestBody || !requestBody.content) return '';
    return Object.keys(requestBody.content)[0];
  }

  const initReqBody = getInitReqBody(requestBody);
  const initContentType = getInitContentType(requestBody);

  const [ reqBody, setReqBody ] = useState(initReqBody);
  const [ paramInfos, setParamInfos ] = useState(parameters);
  const [ basicAuth, setBasicAuth ] = useState('');
  const [ bearerAuth, setBearerAuth ] = useState('');
  const [ apiKey, setApiKey ] = useState('');
  const [ reqContentType, setReqContentType ] = useState(initContentType);

  return (
    <div>
      <Accordion>
        <Accordion.Item eventKey="0">
          <Accordion.Header>
            <Stack direction="horizontal" gap={3}>
              <div><h4><Badge bg={`${color}`}>{requestType.toUpperCase()}</Badge></h4></div>
              <div className="border ms-auto"><h5>{path}</h5></div>
              <div><h6>{summary}</h6></div>
            </Stack>
          </Accordion.Header>
            <Accordion.Body>
              <ExecBtn
                color = {color}
                server = {server}
                path = {path}
                paramInfos = {paramInfos}
                body = {reqBody}
                type = {requestType}
                basicAuth = {basicAuth}
                bearerAuth = {bearerAuth}
                apiKey = {apiKey}
                reqContentType = {reqContentType}
              />
              <SecurityBtn
                security = {security}
                setBasicAuth = {setBasicAuth}
                setBearerAuth = {setBearerAuth}
                setApiKey = {setApiKey}
              />
              <Stack gap={3}>
                <Parameter
                  color = {color}
                  parameters = {parameters}
                  setParamInfos = {setParamInfos}
                />
                <Request
                  color = {color}
                  requestBody = {requestBody}
                  setReqBody = {setReqBody}
                  setReqContentType = {setReqContentType}
                />
                <Response
                  color = {color}
                  responses = {responses}
                />
              </Stack>
            </Accordion.Body>
        </Accordion.Item>
      </Accordion>
    </div>
    );
};

const ExecBtn = ({ color, server, path, paramInfos, body, type, basicAuth, bearerAuth, apiKey, reqContentType }) => {
  if (paramInfos) {
    const pathParams = paramInfos.filter(param => param.in === 'path');
    const queryParams = paramInfos.filter(param => param.in === 'query');
    pathParams.forEach(param => {
      path = path.replace(`{${param.name}}`, param.value);
    });
    queryParams.forEach(param => {
      path = `${path}?${param.name}=${param.value}`;
    });
  }
  const url = server + path;

  const [isServerResShown, setServerResShown] = useState(false);

  const [resHeaders, setResHeaders] = useState({});
  const [resStatus, setResStatus] = useState('');
  const [resData, setResData] = useState('');

  async function sendRequest() {
    try {
      const res = await axios({
        method: type,
        headers: {
          'content-type': reqContentType,
          Auth: basicAuth,
          Authorization: 'Bearer ' + bearerAuth,
          'x-api-key': apiKey,
        },
        url: url,
        data: body, // use axios instead of fetch, because the latter will require body to not exist if its is a get request
      });
      setResHeaders(res.headers);
      setResStatus(res.status);
      setResData(res.data);

      setServerResShown(true);
    } catch (err) {
      setResHeaders(err.response.headers);
      setResStatus(err.response.status);
      setResData(err.response.data);

      setServerResShown(true);

      // console.log(err.message);
      // console.log(err.request);
      // https://stackoverflow.com/questions/45017822/catching-error-body-using-axios-post#answer-63743705
    }
  }

  return (
    <>
      <div className="d-grid gap-2">
        <Button variant="secondary" onClick={sendRequest} >Execute</Button>
      </div>
      {
        isServerResShown
        ?
        <ServerRes
          color = {color}
          method = {type}
          reqUrl = {url}
          reqHeaders = {{'content-type': 'application/json'}} //TODO:
          reqBody = {body}
          resHeaders = {resHeaders}
          resStatus = {resStatus}
          resData = {resData}
        />
        :
        null
      }
    </>
  );
};

const ServerRes = ({ color, method, reqUrl, reqHeaders, reqBody, resHeaders, resStatus, resData }) => {
  return (
    <>
      <div className={`bg-light text-${color}`}><h3>Server Response</h3></div>
      <Table responsive>
        <thead>
          <tr>
            <th>Code</th>
            <th>Body</th>
            <th>Headers</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{resStatus}</td>
            <td><ResponseInput
              exampleValue = {resData}
            /></td>
            <td><ResponseInput
              exampleValue = {resHeaders}
            /></td>
          </tr>
        </tbody>
      </Table>
      <div className={`bg-light text-${color}`}><h3>Original Request</h3></div>
      <Table responsive>
        <thead>
          <tr>
            <th>Method</th>
            <th>Url</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{method}</td>
            <td>{reqUrl}</td>
          </tr>
        </tbody>
      </Table>
      <Table responsive>
        <thead>
          <tr>
            <th>Body</th>
            <th>Headers</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td><ResponseInput
              exampleValue = {reqBody}
            /></td>
            <td><ResponseInput
              exampleValue = {reqHeaders}
            /></td>
          </tr>
        </tbody>
      </Table>
      </>
  );
};

export default Viewer;
