/* eslint-disable no-unused-vars*/

import { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Table from 'react-bootstrap/Table';
import { Button } from 'react-bootstrap';

const Parameter = ( { parameters, color, setParamInfos, getParamInfos } ) => {

  return (
    <div>
      <div className={`bg-light text-${color}`}><h4>Parameters</h4></div>
      <div className={`bg-${color}`}>
        {
          parameters
          ?
          <Table responsive>
            <thead>
              <tr>
                <th>Name</th>
                <th>Description</th>
              </tr>
            </thead>
          {parameters.map((parameter, index) => {
            return (
              <ParamBody
                detail = {parameter}
                setParamInfos = {setParamInfos}
                getParamInfos = {getParamInfos}
                key = {index}
              />
            );
          })}
          </Table>
          :
          'no parameters'
        }
      </div>
    </div>
  );
};

const ParamBody = ({ detail, setParamInfos, getParamInfos }) => {

  const location = detail['in'];
  const { name, schema, required, description, example, examples } = detail;

  function getParamInfo(e) {
    const paramInfo = {
      in: location,
      name: name,
      value: e.target.value,
    };
    getParamInfos(paramInfo);
  }

  function setParam(e) {
    const paramInfo = {
      in: location,
      name: name,
      value: e.target.value,
    };
    setParamInfos(paramInfoArr => {
      let newParamInfoArr = [...paramInfoArr]; //prevent shallow copy
      for (let i = 0; i < paramInfoArr.length; i++) {
        if (newParamInfoArr[i].in === paramInfo.in & newParamInfoArr[i].name === paramInfo.name) {
          newParamInfoArr[i].value = paramInfo.value;
          return newParamInfoArr;
        }
      }
      return [...paramInfoArr, paramInfo];
    });
  }

  // TODO: deal with example

  return (
    <tbody>
      <tr>
        <td>
          <div>{name}{required ? '(required)' : null}</div>
          <div>{schema.type}</div>
          <div>{location}</div>
        </td>
        <td>
          <div>
            <div>{description}</div>
            <div>
              {
                examples
                ?
                <ParamExamples
                  examples = {examples}
                  getParamInfo = {getParamInfo}
                  setParam = {setParam}
                />
                :
                null
              }
              {
                example
                ?
                <ParamExample
                  example = {example}
                  getParamInfo = {getParamInfo}
                  setParam = {setParam}
                />
                :
                null
              }
              {
                !example && !examples
                ?
                <ParamInput
                  getParamInfo = {getParamInfo}
                  setParam = {setParam}
                />
                :
                null
              }
            </div>
          </div>
        </td>
      </tr>
    </tbody>
  );
};

const ParamExamples = ({ examples, setParam, getParamInfo }) => {

  const [examplesValue, setExamples] = useState(Object.keys(examples)[0]);

  function handleExamplesChange(event) {
    setExamples(event.target.value);
    // getParamInfo(event);
    setParam(event);
  }

  return (
    <>
      <div>Examples:</div>
      <div>
        <select onChange={e => handleExamplesChange(e)}>
          {Object.keys(examples).map((example, i) => <option key={i}>{example}</option>)}
        </select>
      </div>
      <div>
        <input type="text" value={examplesValue} onChange={e => handleExamplesChange(e)}></input>
      </div>
    </>
  );
};

const ParamExample = ({ example, setParam, getParamInfo }) => {
  return (
    <>
      <div>Example:</div>
      <div>
        <input type="text" value={example} onChange={e => setParam(e)}></input>
      </div>
    </>
  );
};

const ParamInput = ({ setParam, getParamInfo }) => {
  return (
    <>
      <input type="text" onChange={e => setParam(e)}></input>
    </>
  );
};

export default Parameter;