/* eslint-disable no-unused-vars*/

import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';
import { Container, Row, Col } from 'react-bootstrap';

import { EditorInput } from '../utils/Input';

const Editor = ( { initDoc, docValue, setDoc, switchToContainer } ) => {
  return (
    <>
      <Container>
        <Button href="#viewer" variant="info" onClick={() => switchToContainer('viewer')}>View</Button>
        <Button variant="info">Save</Button>
        <EditorInput
          exampleValue = { initDoc }
          setDoc = { setDoc }
        />
      </Container>
    </>
  );
};

export default Editor;