import { InputGroup, FormControl } from 'react-bootstrap';

const SecurityBtn = ({ security, setBasicAuth, setBearerAuth, setApiKey }) => {
  let isBasicAuthShown = false;
  let isBearerAuthShown = false;
  let isApiKeyAuthShown = false;
  let isCookieAuthShown = false;
  let isOAuthSampleShown = false;
  let isOpenIdShown = false;

  if (security) {
    security.forEach(sec => {
      switch (Object.keys(sec)[0]) {
        case 'basicAuth':
          isBasicAuthShown = true;
          break;
        case 'bearerAuth':
          isBearerAuthShown = true;
          break;
        case 'ApiKeyAuth':
          isApiKeyAuthShown = true;
          break;
        case 'cookieAuth':
          isCookieAuthShown = true;
          break;
        case 'oAuthSample':
          isOAuthSampleShown = true;
          break;
        case 'openId':
          isOpenIdShown = true;
          break;
        default:
          break;
      }
    });
  }

  return(
    <>
      {
        isBasicAuthShown ? <BasicAuthBtn setBasicAuth = {setBasicAuth} />: null
      }
      {
        isBearerAuthShown ? <BearerAuthBtn setBearerAuth = {setBearerAuth} />: null
      }
      {
        isApiKeyAuthShown ? <ApiKeyAuthBtn setApiKey = {setApiKey} />: null
      }
    </>
  );
};

const BasicAuthBtn = ({ setBasicAuth }) => {
  return (
    <>
      <InputGroup className="mb-3">
        <InputGroup.Text id="basic-addon1"></InputGroup.Text>
        <FormControl
          placeholder="auth-content"
          aria-label="auth-content"
          aria-describedby="basic-addon1"
          onChange={(e) => setBasicAuth(e.target.value)}
        />
      </InputGroup>
    </>
  );
};

const BearerAuthBtn = ({ setBearerAuth }) => {
  return (
    <>
      <InputGroup className="mb-3">
        <InputGroup.Text id="basic-addon1">Bearer</InputGroup.Text>
        <FormControl
          placeholder="token"
          aria-label="token"
          aria-describedby="basic-addon1"
          onChange={(e) => setBearerAuth(e.target.value)}
        />
      </InputGroup>
    </>
  );
};

const ApiKeyAuthBtn = ({ setApiKey }) => {
  return (
    <>
      <InputGroup className="mb-3">
        <InputGroup.Text id="basic-addon1"></InputGroup.Text>
        <FormControl
          placeholder="x-api-key"
          aria-label="x-api-key"
          aria-describedby="basic-addon1"
          onChange={(e) => setApiKey(e.target.value)}
        />
      </InputGroup>
    </>
  );
};

export default SecurityBtn;