/* eslint-disable no-unused-vars*/
import { useState, useEffect } from 'react';

import { ResponseInput, RequestInput } from '../utils/Input';

const ReqContent = ({ content, color, setReqBody, setReqContentType }) => {

  const type = 'request';

  const contentTypes = Object.keys(content);
  const [contentTypeBody, setContentTypeBody] = useState(content[contentTypes[0]]);

  // TODO: support multiple content types
  function changeContentTypeBody(contentType) {
    switch (contentType) {
      case 'application/json':
        setContentTypeBody(content[contentType]);
        setReqContentType(contentTypeBody);
        break;
      default:
        break;
    }
  }

  return (
    <div>
      {
        <div>
          <select onChange={e => changeContentTypeBody(e.target.value)}>
            { contentTypes.map((contentType, index) => <option key={index}>{contentType}</option> ) }
          </select>
        </div>
      }
      <ContentTypeBody
          color = {color}
          contentTypeBody = {contentTypeBody}
          type = {type}
          setReqBody = {setReqBody}
      />
    </div>
  );
};

const ResContent = ({ content, color, setReqBody }) => {
  const type = 'response';

  const contentTypes = Object.keys(content);

  const [contentTypeBody, setContentTypeBody] = useState(content[contentTypes[0]]);

  function changeContentTypeBody(contentType) {
    switch (contentType) {
      case 'application/json':
        setContentTypeBody(content[contentType]);
        break;
      default:
        break;
    }
    // TODO: change body based on different content type
  }

  return (
    <div>
      {
        <div>
          <select onChange={e => changeContentTypeBody(e.target.value)}>
            { contentTypes.map((contentType, index) => <option key={index}>{contentType}</option> ) }
          </select>
        </div>
      }
      <ContentTypeBody
          color = {color}
          contentTypeBody = {contentTypeBody}
          type = {type}
          setReqBody = {setReqBody}
      />
    </div>
  );
};

const ContentTypeBody = ( { contentTypeBody, color, type, setReqBody } ) => {

  const { schema, examples, example } = contentTypeBody;

  return (
      <div>
        <div className={`bg-${color}`}>
          {
            examples ?
            <Examples
              data = {examples}
              type = {type}
              setReqBody = {setReqBody}
            />
            : example ?
            <Example
              data = {example}
              type = {type}
              setReqBody = {setReqBody}
            />
            : null
          }
          {
            schema
            ?
            <Schema
              data = {schema}
            />
            :
            null
          }
        </div>
      </div>
  );
};

function getInputType(data, type, setReqBody) {
  switch (type) {
    case 'request':
      return <RequestInput
        exampleValue = {data}
        setReqBody = {setReqBody}
      />;
    case 'response':
      return <ResponseInput exampleValue = {data}/>;
    default:
      return null;
  }
}

const Example = ({ data, type, setReqBody }) => {

  const input = getInputType(data, type, setReqBody);

  return (
    <div>
      <div>Example:</div>
      {input}
    </div>
  );
};

const Examples = ({ data, type, setReqBody }) => {

  const examplesTitles = Object.keys(data);
  const [exampleValue, setExampleValue] = useState( data[examplesTitles[0]] );

  const input = getInputType(exampleValue.value, type, setReqBody);

  return (
    <div>
      <div>Examples:</div>
      <div>
        <select onChange={e => setExampleValue(data[e.target.value])} >
          {
            examplesTitles.map((title, index) => <option key={index}>{title}</option>)
          }
        </select>
      </div>
      {input}
    </div>
  );
};

const Schema = () => {
  return (
    <div>
      <div>Schema: </div>
      <div>Schema is currently under construction</div>
      {/* TODO: Build Schema with reference function (reusable components in swagger) */}
      {/* <ResponseInput
        exampleValue = {data}
      /> */}
    </div>
  );
};

export { ReqContent, ResContent };